import React from 'react';

export default class Cell extends React.Component {


    getValue(){
        if (this.props.value === "#"){
            return "";
        }
        if (this.props.value === "*") {
            return "";
        }
        if(this.props.value === "?" ){
            return null;
        }
        return this.props.value;
    }

    render(){
        let className = "cell" + (this.props.value !== "?" ? "" : " hidden") + (this.props.value === "*"? " is-mine" : "") + (this.props.value === "#" ? " is-flag" : "");


        return (
            <div ref="cell" onClick={this.props.onClick} className={className} onContextMenu={this.props.cMenu}>
                {this.getValue()}
            </div>
        );
    }
}



