import React from 'react';
import ReactDOM from 'react-dom';
import Board from './components/Board';
import './index.css';

class Game extends React.Component {

    state = {
        board: [[]],
        gameState: "",
        mineCount: 0,
        revealed: 0,

        height:8,
        width:8,
        mines:10,

    };

    commonChange = (event) => {
        this.setState({
            [event.target.name]: parseInt(event.target.value, 10)
        });
    };

    selectDefaults = () => {
        let difficulty = document.querySelector("#level_select");
        if (difficulty.value === "beginner") {
            this.setState({
                height: 8,
                width: 8,
                mines: 10,
            });
        }
        if (difficulty.value === "intermediate") {
            this.setState({
                height: 12,
                width: 12,
                mines: 20,
            });
        }
        if (difficulty.value === "expert") {
            this.setState({
                height: 16,
                width: 16,
                mines: 40,
            });
        }
    };
    handleGameStart = () => {
        //also need to set internal state of numb of rows and col

        if (typeof window !== 'undefined') {
            var path = window.location.protocol + '//' + window.location.host + '/api/restart';
        } else {
            path = 'http://localhost:8080/api/restart'
        }

        fetch(path, {
            // fetch('http://localhost:5000/restart', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            credentials: 'include',
            body: JSON.stringify({
                row: this.state.height,
                col: this.state.width,
                mines: this.state.mines,

            }),

        }).then(response => response.json())
            .then(json => {
                this.setState({
                    board: json['result'],
                    gameState: json['game_state'],
                    mineCount: json['mines'],
                    revealed: json['pieces_revealed']
                });
            });

    };

    handleCellClick = (row, col) => {
        //also need to set internal state of numb of rows and col
        if (typeof window !== 'undefined') {
            var path = window.location.protocol + '//' + window.location.host + '/api/step_on';
        } else {
            path = 'http://localhost:8080/api/step_on'
        }
        fetch(path, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',

            },
            body: JSON.stringify({
                row: row,
                col: col,
            }),
            credentials: 'include',
        }).then(response => response.json())
            .then(json => {
                this.setState({
                    board: json['result'],
                    gameState: json['game_state'],
                    mineCount: json['mines'],
                    revealed: json['pieces_revealed']
                });
            });

    };
    componentDidMount(){
        if (typeof window !== 'undefined') {
            var path = window.location.protocol + '//' + window.location.host + '/api/get_current';
        } else {
            path = 'http://localhost:8080/api/get_current'
        }
        fetch(path, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            },
            credentials: 'include',
        }).then(response => response.json())
            .then(json => {
                if (Object.keys(json).length !==0 ){
                    this.setState({
                        board: json['result'],
                        gameState: json['game_state'],
                        mineCount: json['mines'],
                        revealed: json['pieces_revealed']
                    });
                }
            });

    }
    handleCellRightClick = (row,col) => {
        //also need to set internal state of numb of rows and col

        if (typeof window !== 'undefined') {
            var path = window.location.protocol + '//' + window.location.host + '/api/toggle_flag';
        } else {
            path = 'http://localhost:8080/api/toggle_flag'
        }

        fetch(path, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                row: row,
                col: col,

            }),
            credentials: 'include',
        }).then(response => response.json())
            .then(json => {
                this.setState({
                    board: json['result'],
                    gameState: json['game_state'],
                    mineCount: json['mines'],
                    revealed: json['pieces_revealed']
                });
            });

    };

    render() {
        return (
            <div className="game">
                <div className="game-info">
                    <div className="instructions">
                        <h4>Rules</h4>
                        <p>You are presented with a board of squares. Some squares contain mines (bombs), others don't.
                            If you click on a square containing a bomb, you lose. If you manage to click all the squares
                            (without clicking on any bombs) or flag all the mines, you win.</p>
                        <p>Clicking a square which doesn't have a bomb reveals the number of neighbouring squares
                            containing bombs. Use this information plus some guess work to avoid the bombs.</p>
                        <p>To open a square, point at the square and click on it. To mark a square you think is a bomb,
                            point and right-click.</p>
                    </div>
                    <h4>Select a level or customize the board then click "start" to start</h4>
                    <span className="info">Level:
                        <select id="level_select">
                            <option value="beginner"> Beginner </option>
                            <option value="intermediate"> Intermediate </option>
                            <option value="expert"> Expert </option>
                        </select>
                    </span>
                    <button onClick={this.selectDefaults}>Select</button>
                </div>


                <label>Height:
                    <input type="number" name="height" value={this.state.height}  onChange={this.commonChange}/>
                </label>
                <label>Width:
                    <input type="number" name="width" value={this.state.width} onChange={this.commonChange}/>
                </label>
                <label>Mines:
                    <input type="number" name="mines" value={this.state.mines} onChange={this.commonChange}/>
                </label>

                <button onClick={this.handleGameStart}>{  this.state.board === undefined  || this.state.board.length ===0 || this.state.gameState ==="" ?'Start':"Restart" }</button>


                <Board board={this.state.board} cellRightClickHandler={this.handleCellRightClick}
                       cellClickHandler={this.handleCellClick} gameState={this.state.gameState}
                       mineCount={this.state.mineCount} revealed={this.state.revealed}/>

            </div>
        );
    }
}

ReactDOM.render(<Game/>, document.getElementById('root'));



